<template>
    <div class="index">
        <header>
            <div class="logo">
                <img src="@/assets/logo.png" alt="logo">
            </div>
            <top-menu/>
            <el-dropdown @command="handleDrop">
                <span class="el-dropdown-link">
                    {{ username }}<i class="el-icon-arrow-down el-icon--right"></i>
                </span>
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item command="0">资源下载</el-dropdown-item>
                    <el-dropdown-item command="1">退出登录</el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
        </header>
        <div class="layout">
            <transition name="translate">
                <router-view class="child-view padding"/>
            </transition>
        </div>
    </div>
</template>

<script>
import TopMenu from '@/components/topmenu'
import { removeCookie } from '@/libs/auth'
import { mapState } from 'vuex'

export default {
    components: { TopMenu },
    methods: {
        handleDrop(cmd) {
            [
                () => this.$router.push('/proj'),
                () => {
                    removeCookie();
                    this.$router.push('/');
                }
            ][cmd].call(this)
        }
    },
    computed: mapState(['username']),
}
</script>

<style scoped>
.index {
    display: flex;
    flex-direction: column;
}
header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    background: #fff;
}
.logo {
    width: 200px;
}
.layout {
    position: relative;
    height: 100%;
    overflow: hidden;
    flex-shrink: 1;
}
.el-dropdown-link {
    cursor: pointer;
    color: #409EFF;
}
.el-icon-arrow-down {
    font-size: 12px;
}
.translate-enter-active, .translate-leave-active {
	transition: all .4s ease;
}
.translate-enter, .translate-leave-to {
	transform: translateY(20px);
    opacity: 0;
}
</style>
